<template>
  <div id="container">

    <b-modal size="lg" id="my-modal" centered>
      <div class="BA">
        <video width="640px" controls ref="mavideo"></video>
      </div>
    </b-modal>
    <div v-for="value in myJson.movies" :key="value.title">

      <b-card :id='value.id' overlay @mouseover="overme" @mouseleave="outme" :img-src="value.posterUrl" img-alt="Image" img-top tag="article" style="max-width: 20rem;max-height:20rem;" class="mb-1">
        <div class="hideme">
          <b-card-text>
            <p style="padding:0;margin:0;">{{value.plot}}</p><br><br>
            <em style="padding-left:10px;padding-right:10px;">{{value.genres}}</em><br><br>
            <b style="font-size:12px;">{{value.year}}</b>
          </b-card-text>

          <b-button href='#' @click="openBa" variant="primary">Bande Annonce</b-button>
        </div>
      </b-card>
    </div>
  </div>
</template>
<script>
  import Hls from 'hls.js'
  import json from '@/assets/movie.json'
  
  export default {
    name: 'accueil',
    data() {
      return {
        maBA: '',
        myJson: json,
        looking: false,
        maVideo: null
       
      }
    },
    methods: {
      openBa() {
        this.$bvModal.show("my-modal");
        var videos = document.getElementsByTagName("video")
        setTimeout(function() {
          var myvideo = [...videos]
         var videoSrc = 'stream/sample.m3u8'; 
          //var videoSrc = 'https://test-streams.mux.dev/x36xhzz/x36xhzz.m3u8';
          //var videoSrc = 'https://raw.githubusercontent.com/notanewbie/LegalStream/master/packages/sports/all.m3u8';
        if (Hls.isSupported()) {
          console.log("supported")
          var hls = new Hls({debug: true,maxBufferSize: 20});
          hls.loadSource(videoSrc);
          hls.attachMedia( myvideo[0]);
         
          hls.on(Hls.Events.MANIFEST_PARSED, function() {
            myvideo[0].play();
          });
        }
        // hls.js is not supported on platforms that do not have Media Source Extensions (MSE) enabled.
        // When the browser has built-in HLS support (check using `canPlayType`), we can provide an HLS manifest (i.e. .m3u8 URL) directly to the video element through the `src` property.
        // This is using the built-in support of the plain video element, without using hls.js.
        // Note: it would be more normal to wait on the 'canplay' event below however on Safari (where you are most likely to find built-in HLS support) the video.src URL must be on the user-driven
        // white-list before a 'canplay' event will be emitted; the last video event that can be reliably listened-for when the URL is not on the white-list is 'loadedmetadata'.
        else if ( myvideo[0].canPlayType('application/vnd.apple.mpegurl')) {
          console.log("canplay")
           myvideo[0].src = videoSrc;
           myvideo[0].addEventListener('loadedmetadata', function() {
             myvideo[0].play();
          });
        } }, 100);


      },
      overme(event) {

        event.currentTarget.children[1].children[0].classList.add("showme");
      },
      outme(event) {
        event.currentTarget.children[1].children[0].classList.remove("showme");
      }
    },
    mounted() {
      this.$nextTick().then(() => {

        console.log("mounted")
      })
    },
    created() {
      console.log("created")
    }
  }

</script>
<style>
  .card-body {
    flex: 1 1 auto;
    min-height: 0;
    padding: 0 !important;
  }

  .card-img-top {
    max-height: 450px;
