  .hideme {
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    font-size: 12px;
    height: 100%;
    padding-top: 1em;
    cursor: pointer;
    display: none;
  }

  .showme {
    display: block;
  }

</style>
